.boxmodel {
    border: 1px solid black;
    background-color: #303030;
    margin: 10px;
    border-radius: 20px;
    width: calc(33% - 20px);
    height: 400px;
    float: left;
}

.boxmodel-title {
    border-bottom: 1px solid black;
    margin: 0;
    padding: 10px;
}

.boxmodel-image {
    margin: 10px;
    max-width: calc(100% - 20px);
    max-height: 60%;
}

.boxmodel__links {
    border-top: 1px solid black;
    margin: auto;
}