body {
  position: absolute;
  height: 100vh;
  width: 100%;
  margin: 0;
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

.App {
  font-family: sans-serif;
  background: #ffffff;
  color: rgb(255, 255, 255);
  height: 100%;
  box-sizing: border-box;
}

.react-tabs {
  display: flex;
  width: 100%;
  height: 100vh;
  color: rgb(255, 255, 255);
  background: #181818;
}

.react-tabs__tab-list {
  display: flex;
  flex-direction: column;
  width: 15%;
  margin: 0 !important;
  padding: 0;
  color: rgb(255, 255, 255);
  background: #121212;
}

.react-tabs__tab {
  list-style: none;
  cursor: pointer;
  text-align: center;
  color: rgb(255, 255, 255);
  border: 0 !important;
  border-radius: 0 !important;
}

.react-tabs__tab--selected {
  background-color: #202020 !important;
  color: white !important;
}

.react-tabs__tab-panel {
  display: none;
  width: 85%;
  height: 100vh;
  overflow-y: scroll;
}

.react-tabs__tab-panel--selected {
  display: block;
}

.react-tabs__tab {
  padding-left: 24px;
}

.react-tabs__tab--selected {
  padding-left: 21px;
}

.panel-content {
  text-align: center;
  padding-top: 67px;
}

.panel-content > h2 {
  position: fixed;
  left: 15%;
  width: 85vw;
  margin: 0;
  padding: 20px 0px 20px 0px;
  background-color: #121212;
  top: 0;
}